<template>
  <div class="seletor">
    <v-form ref="busca" class="busca">
      <div class="header">
        <span class="search-title">Filtro de busca</span>
        <span class="search-subtitle">Escolha pelo menos um filtro</span>
      </div>
      <v-divider></v-divider>
      <div class="input">
        <v-select
          dark
          label="selecione uma categoria"
          v-model="selected"
          :items="categorias"
          item-value="value"
          item-text="name"
        />

        <v-btn
          :loading="loading"
          dark
          color="blue-grey darken-4"
          v-on:click="buscar()"
          >Buscar</v-btn
        >
      </div>
    </v-form>
  </div>
</template>
<script>
export default {
  name: "SearchForm",

  data() {
    return {
      input: "",
      loading: false,
      opcao1: true,
      selected: 'TODAS',

      categorias: [
        { name: "Educação", value: "EDUCACAO" },
        { name: "Lazer", value: "LAZER" },
        { name: "Saúde", value: "SAUDE" },
        { name: "Festividades", value: "FESTIVIDADES" },
      ],

      localizacao: { centro: "centro", bairro: "bairro" },
    };
  },
  methods: {
    buscar( ) {
      this.$emit("buscar-por", { selected: this.selected });
     

      // this.loading = true;
    },
  },
};
</script>
<style lang="scss" scoped>
.busca {
  position: fixed;
  animation: fade-in 1s ease-in;
}
.header {
  display: flex;
  flex-direction: column;
  .search-title {
    font-size: 2rem;
    font-weight: lighter;
  }
}

.seletor {
  height: 100%;

  padding: 2rem 1rem 3rem 2rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;

  background-color: #3b3b3b;
  color: #efefef;
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  75% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
</style>